<template>
    <h2 class="evfb-heading" :style="{ textAlign: data.align }" v-text="data.value"></h2>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-heading',
        methods: {
            value() {
                return null
            }
        }
    })

    export default Ctrl
</script>


