<template>
    <div class="evfb-app">
        <fb-nav :cols="form.cols"></fb-nav>
        <fb-body :items="form.items"></fb-body>
        <p class="evfb-copyrights">created with ❤ by <a href="https://nocake.io">nocake.io</a></p>
        <ncfs-ctrl-props
            :control.sync="$root.ctrlProps.control"></ncfs-ctrl-props>
        <ncfs-form-settings
            :opened.sync="$root.formSettingsOpened"
            :form="$root.form"></ncfs-form-settings>
        <!-- CONTROL SELECTOR MODAL. -->
        <ncfs-ctrl-selector
            :opened.sync="$root.ctrlSelector.opened"
            :target="$root.ctrlSelector.target"
            :insertMode="$root.ctrlSelector.insertMode"></ncfs-ctrl-selector>
        <!-- EO CONTROL SELECTOR MODAL. -->
        <!-- EXPORT MODAL. -->
        <ncfs-modal :opened.sync="$root.exportModal" title="Get form definition">
            <template #description>
                <p>Below you can find JSON representation of your formsadf.</p>
            </template>
            <template #body>
                <div class="ncfb-textarea">
                    <textarea ref="exportTextarea">{{ getFormDefinition() }}</textarea>
                </div>
            </template>
            <template #footer>
                <button type="button" @click="copy" class="ncfb-btn-ss">Copy</button>
            </template>
        </ncfs-modal>
        <!-- EO EXPORT MODAL. -->
        <!-- IMPORT MODAL. -->
        <ncfs-modal :opened.sync="$root.importModal" title="Import form definition">
            <template #description>
                <p>Paste form definition below to import it.</p>
                <span v-if="importError" class="ncfb-import-error">{{ importError }}</span>
            </template>
            <template #body>
                <div class="ncfb-textarea">
                    <textarea ref="importTextarea"></textarea>
                </div>
            </template>
            <template #footer>
                <button type="button" @click="importJson" class="ncfb-btn-ss">Import</button>
            </template>
        </ncfs-modal>
        <!-- EO IMPORT MODAL. -->
    </div>
</template>

<script>
    import Nav from './components/Nav'
    import Body from './Body.vue'
    import { extend, uuidv4 } from './util/util'

    export default {
        name: 'form-builder',
        props: ['form'],
        components: {
            'fb-nav': Nav,
            'fb-body': Body
        },
        data() {
            return {
                importError: false,
            }
        },
        methods: {
            getFormDefinition() {
                return JSON.stringify(this.form)
            },
            /**
             * Copy textarea content to clipboard.
             */
            copy() {
                this.$refs.exportTextarea.select()
                document.execCommand('copy')
            },
            importJson() {
                const $root = this.$root
                try {
                    const form = JSON.parse(this.$refs.importTextarea.value)
                    form.uuid = uuidv4()
                    $root.form = form
                    $root.importModal = false
                    $root.$emit('change', { type: 'import' })
                } catch (e) {
                    this.importError = 'JSON has bad syntax.'
                }
            },
            call($event) {
                this.$root.ctrlProps.control = null
            }
        },
        mounted() {
            const $refs = this.$refs
            extend(this.$root, {
                previewModal: $refs.preview
            })
        }
    }
</script>

<style>
    @import 'css/reset.css';
    @import 'css/general.css';
    @import 'css/modal.css';

    [evfbhint]{
        /* Must be !important because reset.css override this. */
        position: relative !important;
        display: inline-block;
    }
    [evfbhint]::before {
        content: "";
        position: absolute;
        top:-6px;
        left:50%;
        transform: translateX(-50%);
        border-width: 4px 6px 0 6px;
        border-style: solid;
        border-color: rgba(0,0,0,0.7) transparent transparent     transparent;
        z-index: 99;
        opacity:0;
    }

    [evfbhint-position='left']::before{
        left:0%;
        top:50%;
        margin-left:-12px;
        transform: translatey(-50%) rotate(-90deg)
    }
    [evfbhint-position='top']::before{
        left:50%;
    }
    [evfbhint-position='buttom']::before{
        top:100%;
        margin-top:8px;
        transform: translateX(-50%) translatey(-100%) rotate(-180deg)
    }
    [evfbhint-position='right']::before{
        left:100%;
        top:50%;
        margin-left:1px;
        transform:translatey(-50%) rotate(90deg)
    }

    [evfbhint]::after {
        content: attr(evfbhint);
        position: absolute;
        left: 50%;
        top: -6px;
        transform: translateX(-50%) translateY(-100%);
        background: rgba(0,0,0,0.9);
        text-align: center;
        color: #fff;
        font-size: .8rem;
        min-width: 80px;
        pointer-events: none;
        padding: 10px 4px;
        z-index:99;
        opacity:0;
    }

    [tooltip-position='left']::after{
        left:0%;
        top:50%;
        margin-left:-8px;
        transform: translateX(-100%)   translateY(-50%);
    }
    [tooltip-position='top']::after{
        left:50%;
    }
    [tooltip-position='buttom']::after{
        top:100%;
        margin-top:8px;
        transform: translateX(-50%) translateY(0%);
    }
    [tooltip-position='right']::after{
        left:100%;
        top:50%;
        margin-left:8px;
        transform: translateX(0%)   translateY(-50%);
    }

    [evfbhint]:hover::after,[evfbhint]:hover::before {
        opacity:1
    }
</style>
<style scoped>
    .evfb-app {
        position: relative;
    }
    .evfb-copyrights {
        font-size: .7rem;
        color: gray;
        text-align: right;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .evfb-copyrights a {
        color: #88A7F1;
        font-size: .7rem;
    }

    .ncfb-textarea {
        padding: 1vw 2vw;
    }
    .ncfb-textarea textarea {
        width: 100%;
        height: 220px;
    }
    .ncfb-import-error {
        color: red;
        margin-top: 20px;
        display: block;
    }
    .ncfb-import-error:empty {
        display: none;
    }
</style>


