<template>
    <ncfs-modal :opened="control !== null" @update:opened="$emit('update:control', null)" :title="control ? control.label : ''">
        <template #description>
            <h2 v-if="control"><strong>{{ $root.getCtrlDef(control.type).info.name }}</strong></h2>
        </template>
        <template #body>
            <component
                v-if="control"
                :is="$root.getCtrlDef(control.type).props"
                :data="control"></component>
        </template>
    </ncfs-modal>
</template>

<script>
    /**
     * Control properties modal. This is "modal" component, which displays
     * control properties editor.
     */
    import Vue from 'vue'
    import FbIcon from './ui/Icon'
    import { isObj } from './../util/util'

    export default Vue.component('ncfs-ctrl-props', {
        components: { FbIcon },
        props: {
            // Control data passed to control properties editor.
            control: {
                required: false,
                validator: prop => isObj(prop) || prop === null
            }
        }
    })
</script>


