<template>
    <div class="evfb-radios evfb-ctrl" :class="{ 'evfb-ctrl-error': error }">
        <label class="evfb-lbl" v-if="data.label">
            {{ data.label }}
            <span v-if="data.help" class="evfb-help">{{ data.help }}</span>
        </label>
        <div class="evfb-input-cnt" :class="[layout__]">
            <div v-for="(group, groupIndex) in items__" :class="'evfb-col evfb-col-'+(groupIndex+1)">
                <div class="evfb-radio" v-for="item in group">
                    <ui-radio :name="'rc-'+data.uid" :value="item.value" :label="item.value" :checked="data.default" ref="input"></ui-radio>
                </div>
            </div>
        </div>
        <span class="evfb-error" v-show="error">{{ error }}</span>
    </div>
</template>

<script>
    import Vue from 'vue'
    import AbstractControl from './../../renderer/AbstractControl'
    import UiRadio from "../../ui/Radio"
    import { each } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        components: { UiRadio },
        name: 'fb-radios',
        methods: {
            getValue() {
                let value = null

                each(this.$refs.input, (el) => {
                    if (el.$refs['input'].checked) {
                        value = el.value
                    }
                })
                return value
            }
        },
        computed: {
            items__() {
                const
                    me = this,
                    data = me.data,
                    layout = data.layout,
                    colNum = (layout === '1-column' || layout === 'side-by-side') ? 1
                        : (layout === '2-column' ? 2
                            : (layout === '3-column' ? 3 : 1)),
                    arr = [],
                    items = data.items,
                    size = Math.ceil(items.length / colNum);
                let i = 0
                for (; i < items.length; i += size) {
                    arr.push(items.slice(i, i+size))
                }
                return arr
            },
            layout__() {
                return 'evfb-layout-' + this.data.layout
            }
        }
    })

    Ctrl.ALIAS = 'radio'

    export default Ctrl
</script>

<style scoped>
    .evfb-radios {
        display: block;
        vertical-align: middle;
        margin-bottom: 16px;
    }
    .evfb-opt-label {
        font-size: .8em;
        margin-bottom: 20px;
        display: block;
    }
    .evfb-lbl {
        font-size: .8rem;
        vertical-align: middle;
    }

    .evfb-layout-2-column {
        font-size: 0;
    }
    .evfb-layout-2-column .evfb-col {
        display: inline-block;
        width: 50%;
        font-size: 16px;
        vertical-align: top;
    }
    .evfb-layout-3-column .evfb-col {
        display: inline-block;
        width: 33%;
        font-size: 16px;
        vertical-align: top;
    }
    .evfb-layout-side-by-side .evfb-radio {
        display: inline-block;
        margin-right: 20px;
    }
</style>


