import axios from 'axios'

const reqs = {}

function cancel(name, reason = '') {
    if (reqs[name]) {
        reqs[name].cancel(reason)
        reqs[name] = null
    }
}

function cancelAll(prefix, reason = '') {
    for (let key in reqs) {
        if (reqs.hasOwnProperty(key) && key.indexOf(prefix+':') === 0 && reqs[key] !== null) {
            reqs[key].cancel(reason)
        }
    }
}

export default function(name, o, scope) {
    if (typeof o === 'string') {
        if (o === 'cancel') {
            cancel(name, scope)
        }
        if (o === 'cancelAll') {
            cancelAll(name, scope)
        }

        return
    }

    const me = scope

    if (o.onlyIfNotActive && reqs[name]) {
        return
    }

    // Cancel previous request.
    cancel(name)

    const promise = new Promise((resolve, reject) => {
        // Create cancel token.
        const source = axios.CancelToken.source()
        reqs[name] = source
        o.cancelToken = source.token

        if (o.type && !o.method) {
            o.method = o.type.toUpperCase()
        }

        if (!o.method || o.method.toLowerCase() === 'get') {
            if (o.data && !o.params) {
                o.params = o.data
                delete o.data
            }
        }

        const req = axios(o)
        req
            .then((response) => {
                if (reqs[name] !== null) {
                    reqs[name] = null
                    o.success && o.success.call(me, response)
                    o.complete && o.complete.call(me, response)
                    resolve(response)
                }
            })
            .catch((error) => {
                if (error.__CANCEL__) {
                    return
                }

                if (reqs[name] !== null) {
                    reqs[name] = null

                    o.error && o.error.call(me, error)
                    o.complete && o.complete.call(me, error)
                    reject(error)
                }
            })
    })

    return promise
}


