<template>
    <label class="evfb-lbl" v-if="label">
        {{ label }}
        <span v-if="help && $root.form.lblPos !== 'left'" class="evfb-help">{{ help }}</span>
    </label>
</template>
<script>
    export default {
        props: {
            label: String,
            help: String
        }
    }
</script>


