<template>
    <div class="evfb-select evfb-ctrl" :class="{ 'evfb-error-ctrl': error }">
        <component :is="cmpLbl" :label="data.label" :help="data.help"></component>
        <select class="evfb-input"
                ref="input"
                :class="{ 'evfb-input-error': error }">
            <option v-for="item in data.values" :key="item.uid" :value="item.uid">{{ item.value }}</option>
        </select>
        <span v-if="data.help && $root.form.lblPos === 'left'" class="evfb-help">{{ data.help }}</span>
        <span class="evfb-error" v-show="error">{{ error }}</span>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../../renderer/AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-select',
        methods: {
            getValue() {
                return this.$refs.input.value
            }
        },
        mounted() {
            this.$refs.input.value = this.data.selected
        }
    })

    Ctrl.ALIAS = 'select'

    export default Ctrl
</script>


